import { injectGlobal } from "react-emotion";

import "../styles/base";

const baseFontSize = `18px`;
const smallFontSize = `${baseFontSize * 0.875}`;

const spacingUnit = `30px`;

const greyColor = `#828282`;
const greyColorLight = `#afafaf`; //lighten($grey-color, 40%);
const greyColorDark = `darken(${greyColor}, 25%)`;

const onPalm = `600px`;
const onLaptop = `800px`;

injectGlobal`
  /**
  * Site header
  */
  .site-header {
      // border-top: 5px solid ${greyColorDark};
      //border-bottom: 1px solid ${greyColorLight};
      min-height: 56px;

      // Positioning context for the mobile navigation icon
      position: relative;
  }

  .site-title {
      font-size: 26px;
      line-height: 56px;
      letter-spacing: -1px;
      margin-bottom: 0;
      float: left;

      &,
      &:visited {
          color: ${greyColorDark};
      }
  }

  /**
  * Site footer
  */
  .site-footer {
      border-top: 1px solid ${greyColorLight};
      padding: ${spacingUnit} 0;
  }

  .footer-heading {
      font-size: 18px;
      margin-bottom: ${spacingUnit} / 2;
  }

  .contact-list,
  .social-media-list {
      list-style: none;
      margin-left: 0;
  }

  .rss-subscribe {
    text-align: right;
  }

  .footer-col-wrapper {
      font-size: ${baseFontSize} - 3;
      color: ${greyColor};
      margin-left: -${spacingUnit} / 2;
      @extend %clearfix;
  }

  .footer-col {
      float: left;
      margin-bottom: ${spacingUnit} / 2;
      padding-left: ${spacingUnit} / 2;
  }

  .footer-col-1 {
      width: -webkit-calc(35% - (#{${spacingUnit}} / 2));
      width:         calc(35% - (#{${spacingUnit}} / 2));
  }

  .footer-col-2 {
      width: -webkit-calc(20% - (#{${spacingUnit}} / 2));
      width:         calc(20% - (#{${spacingUnit}} / 2));
  }

  .footer-col-3 {
      width: -webkit-calc(45% - (#{${spacingUnit}} / 2));
      width:         calc(45% - (#{${spacingUnit}} / 2));
  }

  @include media-query(${onLaptop}) {
      .footer-col-1,
      .footer-col-2 {
          width: -webkit-calc(50% - (#{${spacingUnit}} / 2));
          width:         calc(50% - (#{${spacingUnit}} / 2));
      }

      .footer-col-3 {
          width: -webkit-calc(100% - (#{${spacingUnit}} / 2));
          width:         calc(100% - (#{${spacingUnit}} / 2));
      }
  }

  @include media-query($on-palm) {
      .footer-col {
          float: none;
          width: -webkit-calc(100% - (#{${spacingUnit}} / 2));
          width:         calc(100% - (#{${spacingUnit}} / 2));
      }
  }



  /**
  * Page content
  */
  .page-content {
      padding: ${spacingUnit} 0;
  }

  .page-heading {
      font-size: 18px;
      font-weight: bold;
  }

  .post-list {
      margin-left: 0;
      list-style: none;

      > li {
          margin-bottom: 0; /*${spacingUnit};*/
      }
  }

  .archive-group {
      margin-left: 0;
      list-style: none;

      > li {
          margin-bottom: 0; /*${spacingUnit};*/
      }
  }

  .post-meta {
      font-size: ${smallFontSize};
      color: ${greyColor};
      float: right;
      font-size: 0.8em;
  }

  .post-link {
  /*
      display: block;
      font-size: 24px;
  */
      font-size: 0.88em;
      font-weight: 400;
  }

  .post-desc {
      margin: 0 0 10px 14px;
      font-size: 0.76em;
      color: #999;
      font-weight: 300;
  }


  /**
  * Posts
  */
  .post-header {
      .post-meta {
          font-size: ${smallFontSize};
          color: ${greyColor};
          float: none;
      }
      margin-bottom: ${spacingUnit};
  }

  .post-title {
      font-size: 42px;
      letter-spacing: -1px;
      line-height: 1;

      @include media-query(${onLaptop}) {
          font-size: 36px;
      }
  }

  .post-content {
    margin-bottom: ${spacingUnit};
    font-size: 16px;

    h1 {
      padding-top: 20px;
    }
    h2 {
        font-size: 30px;

        @include media-query(${onLaptop}) {
            font-size: 28px;
        }
    }

    h3 {
        font-size: 26px;

        @include media-query(${onLaptop}) {
            font-size: 22px;
        }
    }

    h4 {
        font-size: 20px;

        @include media-query(${onLaptop}) {
            font-size: 18px;
        }
    }

    hr {
      border: none;
      border-top: 1px solid ${greyColor};
      margin-top: 30px;
    }

    p {
      margin: 20px 0;
    }
  }

  img[alt=drawing] { width: 200px; }

`;
